$target-fade-bg: $c-highlight !default;

$target-fade-from-color: rgba($target-fade-bg, 0.4);
$target-fade-to-color: rgba($target-fade-bg, 0);
/// Settings for common animation.

@keyframes bounceInUp {
    0% {
        opacity: 0;
        transform: translateY(2000px);
    }

    60% {
        opacity: 1;
        transform: translateY(-30px);
    }

    80% {
        transform: translateY(10px);
    }

    100% {
        transform: translateY(0);
    }
}

@keyframes spin {
    from {
        transform: rotate(0deg);
    }

    to {
        transform: rotate(360deg);
    }
}

@keyframes popin {
    from {
        transform: scale(0.95) translateY(10px);
    }

    to {
        transform: rotate(1) translateY(-10px);
    }
}

@keyframes popinRight {
    from {
        transform: scale(0.95) translateX(-10px);
    }

    to {
        transform: rotate(1) translateX(10px);
    }
}

@keyframes popinDown {
    from {
        transform: scale(0.95) translateY(-10px);
    }

    to {
        transform: rotate(1) translateY(10px);
    }
}

@keyframes popinLeft {
    from {
        transform: scale(0.95) translateX(10px);
    }

    to {
        transform: rotate(1) translateX(-10px);
    }
}

@keyframes target-fade {
    0% {
        background-color: $target-fade-from-color;
    }

    100% {
        background-color: $target-fade-to-color;
    }
}
