/// @created  20/06/2016
////

$button-secondary-bg-color: $c-dark-15 !default;
$button-secondary-color: $c-light !default;
$button-secondary-bg-color-hover: lighten(
    $button-secondary-bg-color,
    7.5%
) !default;
$button-secondary-color-hover: $button-secondary-color !default;
$button-secondary-bg-color-active: lighten(
    $button-secondary-bg-color-hover,
    7.5%
) !default;
$button-secondary-color-active: $button-secondary-color !default;
$button-secondary-trans-speed: $button-trans-speed !default;
$button-secondary-disabled-color: rgba($button-secondary-color, 0.65) !default;

.c-button--secondary {
    border-color: $button-secondary-bg-color;

    &:not([disabled]) {
        @include color-swap-on-hover(
            $button-secondary-color $button-secondary-bg-color,
            $button-secondary-color-hover $button-secondary-bg-color-hover,
            $button-secondary-trans-speed
        );

        &:active {
            background-color: $button-secondary-bg-color-active;
            color: $button-secondary-color-active;
        }
    }

    &.is-active {
        background-color: $button-secondary-bg-color-active;
        color: $button-secondary-color-active;

        &:hover,
        &:focus {
            background-color: $button-secondary-bg-color-active;
            color: $button-secondary-color-active;
        }
    }

    &[disabled] {
        background: lighten($button-secondary-bg-color, 15%);
        color: $button-secondary-disabled-color;
    }

    svg {
        fill: $button-secondary-color;
    }
}
